<template>
  <table class="offer-create-table" v-if="products.length && !$isMobile">
    <thead>
      <tr>
        <th>Артикул</th>
        <th>Наименование</th>
        <th>Цена</th>
        <th>Кол-во</th>
        <th>Скидка</th>
        <th>Стоимость</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(p, i) in products" :key="i">
        <td>
          <span>
            {{ p.article }}
          </span>
          <span class="tooltip">
            {{ p.article }}
          </span>
        </td>
        <td>
          <span>
            {{ p.name }}
          </span>
          <span class="tooltip">
            {{ p.name }}
          </span>
        </td>
        <td class="no-wrap">
          <base-input :type="'text'" :tableInput="true" v-model="p.price" />
        </td>
        <td class="no-wrap">
          <base-input :type="'text'" :tableInput="true" v-model="p.quantity" />
        </td>
        <td class="no-wrap">
          <base-input :type="'text'" :tableInput="true" v-model="p.discount" />
        </td>
        <td>
          <span> {{ price(p.price) * quantity(p.quantity) }} ,00 ₽ </span>
          <span class="tooltip">
            {{ price(p.price) * quantity(p.quantity) }} ,00 ₽
          </span>
        </td>
        <td class="no-wrap">
          <span
            class="_remove"
            @click="
              () => {
                $emit('removeProduct', p.id);
              }
            "
          >
            <img src="@/assets/icons/clear.svg" alt="Remove" />
          </span>
        </td>
      </tr>
    </tbody>
  </table>
  <div
    class="offer-create-table-mobile table-mobile"
    ref="table"
    v-else-if="products.length && $isMobile"
  >
    <div class="table-mobile-column" v-for="(p, i) in products" :key="i">
      <span
        class="_remove"
        @click="
          () => {
            $emit('removeProduct', p.id);
          }
        "
      >
        <img src="@/assets/icons/clear.svg" alt="Remove" />
      </span>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Артикул </span>
        <span class="table-mobile__value">
          {{ p.article }}
        </span>
      </div>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Наим-ие </span>
        <span class="table-mobile__value">
          {{ p.name }}
        </span>
      </div>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Цена </span>
        <span class="table-mobile__value">
          <base-input :type="'text'" :tableInput="true" v-model="p.price" />
        </span>
      </div>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Скидка </span>
        <span class="table-mobile__value">
          <base-input :type="'text'" :tableInput="true" v-model="p.quantity" />
        </span>
      </div>
      <div class="table-mobile-row">
        <span class="table-mobile__title"> Стоимость </span>
        <span class="table-mobile__value">
          <base-input :type="'text'" :tableInput="true" v-model="p.discount" />
        </span>
      </div>
    </div>
  </div>
  <div class="offer-create-price">
    <div>
      <div class="offer-create-price__discount">
        <span> Скидка </span>
        <span> {{ totalDiscountedPrice }},00 ₽ </span>
      </div>
      <div class="offer-create-price__total">
        <span> Общая стоимость </span>
        <span> {{ totalPrice - totalDiscountedPrice }},00 ₽ </span>
      </div>
    </div>
  </div>
</template>

<script>
import BaseInput from "@/components/common/Base.Input.vue";

import { getNumber } from "@/helpers/getNumber";

export default {
  name: "OfferCreateTable",
  props: {
    products: {
      type: Array,
    },
  },
  components: {
    BaseInput,
  },
  computed: {
    price() {
      return (p) => {
        return getNumber(p.slice(0, -4));
      };
    },
    quantity() {
      return (q) => {
        return getNumber(q);
      };
    },
    totalDiscountedPrice() {
      let price = 0;
      console.log();
      this.products.forEach(
        (p) =>
          (price +=
            (getNumber(p.price.slice(0, -4)) * getNumber(p.quantity) * 30) /
            100)
      );
      return price;
    },
    totalPrice() {
      let price = 0;
      this.products.forEach(
        (p) =>
          (price += getNumber(p.price.slice(0, -4)) * getNumber(p.quantity))
      );
      return price;
    },
  },
  emits: ["removeProduct"],
};
</script>

